import type { AriaListBoxOptions } from '@react-aria/listbox';
import type { Node } from '@react-types/shared';
import composeRefs from '@seznam/compose-react-refs';
import { motion } from 'framer-motion';
import React, { createContext, useContext, useEffect, useMemo, useRef } from 'react';
import { useFocusRing, useListBox, useOption as useOptionAria } from 'react-aria';
import { ListProps, ListState, useListState } from 'react-stately';
import { createFramerTransition } from '../../../hooks/transitions';
import { clsx } from '../../../libs/clsx';
import { validateCollection } from '../../../libs/collections';
import { createErrorWithCode } from '../../../libs/exceptions';
import { createGenericComponent } from '../../../libs/generic-component';
import { mergeProps } from '../../../libs/merge-props';

import styles from './list-box.less';

const listboxTransition = /* @__PURE__ */ createFramerTransition().withVariants({
  initial: {
    opacity: 0,
    scale: 0.96,
    rotateX: '-7deg',
    rotateY: '0deg',
    transformOrigin: 'top center',
    transition: { type: 'tween', ease: 'easeInOut', duration: 0.2 },
    perspective: '500px',
  },

  animate: {
    opacity: 1,
    scale: 1,
    rotateX: '0deg',
    rotateY: '0deg',
    transition: { type: 'tween', ease: 'easeInOut', duration: 0.2 },
    perspective: '500px',
  },
});

interface ListBoxContext<T extends object = object> {
  state?: ListState<T>;
  ref?: React.MutableRefObject<HTMLUListElement | null>;
}

export const ListBoxContext = /* @__PURE__ */ createContext<ListBoxContext | null>(null);

interface ListBoxProps<T extends object> extends ListProps<T>, AriaListBoxOptions<T> {
  minHeight?: React.CSSProperties['maxHeight'];
  maxHeight?: React.CSSProperties['maxHeight'];
}

function ListBoxRender<T extends object>(props: ListBoxProps<T>) {
  const { label, minHeight, maxHeight } = props;

  // Validate props & children
  if (!label && !props['aria-label'] && !props['aria-labelledby']) {
    throw createErrorWithCode(
      'LIST_BOX_MISSING_ACCESSIBILTY_LABEL',
      GenericListBox,
      'If you do not provide a visible `label`, you must specify an `aria-label` or `aria-labelledby` attribute for accessibility',
    );
  }

  const validCollectionChildren = validateCollection(props, GenericListBox);

  // Create listbox state & refs
  const { state: contextualState, ref: contextualRef } = useContext(ListBoxContext) ?? ({} as ListBoxContext);
  const listboxRef = useRef<HTMLUListElement | null>(null);
  const state = contextualState ?? useListState({ ...props, children: validCollectionChildren });
  const { listBoxProps, labelProps } = useListBox(props, state, listboxRef);

  // listbox animation
  const getListBoxTransitionProps = listboxTransition.use({
    initial: 'initial',
    animate: 'animate',
    exit: 'initial',
  });

  return (
    <motion.div className={styles.ListBox} {...getListBoxTransitionProps()}>
      {label && <div {...mergeProps({ className: styles.label }, labelProps)}>{label}</div>}
      <ul {...listBoxProps} style={{ minHeight, maxHeight }} ref={composeRefs(listboxRef, contextualRef)}>
        {[...state.collection].map((item) => (
          <Option key={item.key} item={item} state={state} />
        ))}
      </ul>
    </motion.div>
  );
}

const OptionContext = /* @__PURE__ */ createContext<{
  isFocused: boolean;
  isSelected: boolean;
  isPressed: boolean;
  isDisabled: boolean;
}>({ isFocused: false, isSelected: false, isPressed: false, isDisabled: false });

export function useOption() {
  return useContext(OptionContext);
}

interface OptionProps<T extends object> {
  item: Node<T>;
  state: ListState<T>;
}

function Option<T extends object>(props: OptionProps<T>) {
  const { item, state } = props;

  // Derive option state and create refs
  const ref = React.useRef<HTMLLIElement | null>(null);
  const { optionProps, isFocused, isSelected, isPressed, isDisabled } = useOptionAria({ key: item.key }, state, ref);
  const { isFocusVisible, focusProps } = useFocusRing();

  // Create a context wrapping items with critical metadata
  const ctx = useMemo(
    () => ({ isFocused, isSelected, isPressed, isDisabled }),
    [isFocused, isSelected, isPressed, isDisabled],
  );

  useEffect(() => {
    if (isFocused) {
      ref.current?.scrollIntoView({ behavior: 'smooth', block: 'nearest', inline: 'nearest' });
    }
  }, [isFocused]);

  const isInListBoxContext = !!useContext(ListBoxContext);

  return (
    <OptionContext.Provider value={ctx}>
      <li
        {...mergeProps(
          {
            className: clsx(
              styles.Option,
              isSelected && styles.selected,
              isFocused && (isFocusVisible || isInListBoxContext) && styles.focused,
            ),
          },
          optionProps,
          focusProps,
        )}
        ref={ref}
      >
        {item.rendered}
      </li>
    </OptionContext.Provider>
  );
}

const GenericListBox = /* @__PURE__ */ createGenericComponent('ListBox', ListBoxRender);

export const ListBox = /* @__PURE__ */ Object.assign(GenericListBox, { useOption });
